<template>
  <div>
    <el-dialog :title="update==true?'菜单添加':'菜单修改'" :visible.sync="dialogFormVisible">
      
      <el-form :model="form">
        <el-form-item
          label="菜单名称"
          :label-width="formLabelWidth"
        >
          <el-input v-model="form.title" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="上级菜单" :label-width="formLabelWidth">
          <el-select v-model="form.pid" @change="dis">
            <el-option label="顶级菜单" :value="0"></el-option>
            <el-option :label="item.title" :value="item.id"
            v-for="(item,index) in tableData" :key="index"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="菜单类型" :label-width="formLabelWidth">
          <el-radio v-model="form.type" :label="1" disabled>目录</el-radio>
          <el-radio v-model="form.type" :label="2" disabled>菜单</el-radio>
        </el-form-item>

        <el-form-item label="菜单图标" v-if="form.type==1">
          <el-select v-model="form.icon" placeholder="请选择菜单图标">
            <el-option v-for="(item,index) in icons" :key="index" :value="item">
              <i :class="item"></i>
            </el-option>
          </el-select>
        </el-form-item>
        
        <el-form-item label="菜单地址" v-if="form.type==2">
          <el-select v-model="form.url" placeholder="请选择菜单地址">
            <el-option v-for="(item,index) in indexRouters" :key="index" :value="'/'+item.path" :label="item.name">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="状态">
            
          <el-switch
            v-model="form.status"
            :active-value="1"
            :inactive-value="2"
            active-color="#13ce66"
            inactive-color="#ff4949">
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle">取 消</el-button>
        <el-button type="primary" @click="addMenuList" v-if="update">添加</el-button>
        <el-button type="primary" @click="updateMenu" v-else>修改</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import {indexRouters} from '../../../router/index'
import {alertSuccess,alertWarn} from '../../../utils/alert'
import { menuListAdd ,updateMenuAct,menuinfo} from "../../../utils/request";
export default {
  props: ['update'],
  components: {},
  data() {
    return {
      dialogFormVisible: false,
      form: {
        pid: 0,
        title: "",
        icon: "",
        type:1,
        url: "",
        status: 0,
      },
      icons: [
        "el-icon-s-tools",
        "el-icon-user",
        "el-icon-camera",
        "el-icon-message-solid",
      ],
      indexRouters:indexRouters,
      formLabelWidth: "120px",
    };
  },
  computed: {
    ...mapGetters({
      tableData:"menuList"
    }),
  },
  methods: {
    ...mapActions({
        getMenuList:"getMenuList"
    }),
    cancle(){
        this.dialogFormVisible=false;
        this.clearForm()
    },
    clearForm(){
      this.form={
        pid: 0,
        title: "",
        icon: "",
        type: 0,
        url: "",
        status: 0,
      }
    },
    addMenuList() {
      this.dialogFormVisible = false;
      menuListAdd(this.form).then(res=>{
        console.log(res);
        this.getMenuList()
        this.clearForm()
      })
    },
    dis(){
      if(this.form.pid===0){
        this.form.type=1
      }else{
        this.form.type=2
      }
    },
    getMenuDetail(id){
      menuinfo(id).then(res=>{
        this.form=res.data.list
        this.form.id=id
      })
    },
    updateMenu(){
      updateMenuAct(this.form).then(res=>{
        if(res.data.code==200){
                alertSuccess(res.data.msg)
                this.dialogFormVisible = false;
                this.clearForm()
                this.getMenuList()
              }else{
                alertWarn(res.data.msg)
              }
      })
    },
    
  },
  mounted(id) {
      
  },
};
</script>
<style scoped>
</style>


